import Vue from 'vue'
import VueRouter from 'vue-router'
import professorLogin from '../views/professor/login.vue'
import professorPage from "@/views/professor/professorPage"
import totalView from "@/views/professor/totalView";
import informationEdu from "@/views/professor/informationEdu";
import informationWork from "@/views/professor/informationWork";
import informationSkill from "@/views/professor/informationSkill";
import jianliProfessor from "@/views/professor/jianliProfessor";
import evaluate from "@/views/professor/evaluate";
import jianli from "@/views/professor/jianli";
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'professorLogin',
    component: professorLogin,
  },
    {
        path:'/professorPage',
        name:'professorPage',
        redirect: '/totalView',
        component:professorPage,
        children: [
            {
                path: '/totalView',
                name:'totalView',
                component: totalView,
            },
            {
                path: '/informationEdu',
                name: 'informationEdu',
                component: informationEdu,
            },
            {
              path: '/informationWork',
              name:'informationWork',
              component: informationWork,
            },
            {
                path: '/informationSkill',
                name:'informationSkill',
                component: informationSkill,
            },
            {
              path:'/jianliProfessor',
              name:'jianliProfessor',
              component: jianliProfessor,
            },
            {
                path:'/evaluate',
                name:'evaluate',
                component: evaluate,
            },
            {
                path:'/jianli',
                name:'jianli',
                component:jianli,
            },
            {
                path:'/logout',
                name:'logout',
                component:() =>
                    import('@/views/professor/login.vue')
            },
        ]
    },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to,from,next) =>{
      if(to.path ==='/'){//to:跳转路径 path：路径 name：命名导航
        return next();
      }
      let username = window.sessionStorage.getItem("username");
      let password = window.sessionStorage.getItem("password");

      if(username === "zhangsan" && password === "123456"){
        next()
      }else{
        next({path:'/'})
      }
    }

)
export default router
